nav {
    background: rgba(0, 0, 0, 0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    right: -74px;
    transform: translateX(-50%);
    bottom: 4.6%;
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);

    ul {
        display: flex;
        gap: 0.7rem;

        a {
            background: transparent;
            padding: 0.9rem;
            border-radius: 50%;
            display: flex;
            color: var(--color-light);
            font-size: 1.9rem;
        }

        a:hover {
            background: rgba(0, 0, 0, 0.3);
        }

        a.active {
            background: var(--color-primary);
            color: var(--color-bg);
        }
    }
}