@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
    outline: none;
    list-style: none;
    text-decoration: none;
    // cursor: url(./assets/icons/my-cursor.png), auto;
}

:root {
    --color-bg: #000000;
    --color-bg-variant: #47637A;
    --color-primary: #00FFEB;
    --color-primary-variant: #0E66AD;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 400ms ease;

    --container-width-lg: 75%;
    --container-width-md: 86%;
    --container-width-sm: 90%;
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
    font-family: 'Poppins', sans-serif;
}

::-webkit-scrollbar {
    display: none;
}

body {
    background: var(--color-bg);
    color: var(--color-white);
    line-height: 1.7;
    // background-image: url(../src/assets/bg-texture.png);
}

/* ================== GENERAL STYLES ================== */

.container {
    width: var(--container-width-lg);
    margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: 600;
}

h1 {
    font-size: 2.5rem;
}

section {
    margin-top: 8rem;
    // min-height: 100vh;
}

section>h2,
section>h5 {
    text-align: center;
    color: var(--color-light);
}

section>h2 {
    color: var(--color-primary);
    margin-bottom: 3rem;
}

.text-light {
    color: var(--color-light);
}

a {
    color: var(--color-primary);
    transition: var(--transition);
}

a:hover {
    color: var(--color-bg-variant);
}
button{
    background-color: hsla(0, 0%, 0%, 0.6);
}
.btn {
    width: max-content;
    background-color: hsla(0, 0%, 0%, 0.6);
    display: inline-block;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn:hover {
    background: var(--color-bg-variant);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: var(--color-primary);
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
} 

/* =============== MEDIA QUERIES (MEDIUM DEVICES) =============== */

@media screen and (max-width: 1024px) {
    .container {
        width: var(--container-width-md);
    }

    section {
        margin-top: 6rem;
    }
}

/* =============== MEDIA QUERIES (SMALL DEVICES) =============== */

@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }

    section>h2 {
        margin-top: 2rem;
    }
}

.icon-fixed {
    position: fixed;
    left: 1rem;
    bottom: 1rem;
    z-index: 1;
    margin: 1rem;
    font-size: 6rem;
    color: #00FFEB;
    box-shadow: -7px 7px 37px 1px rgba(0, 0, 0, 0.6) inset;
    -webkit-box-shadow: -7px 7px 37px 1px rgba(0, 0, 0, 0.6) inset;
    -moz-box-shadow: -7px 7px 37px 1px rgba(0, 0, 0, 0.6) inset;
    border-radius: 50% 50% 50% 0%;

    :hover {
        color: var(--color-bg-variant);
    }
}

